* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/*
* Header
*/
.header {
  display: flex;
  align-items: center;
  padding: 1rem;
  a {
    text-decoration: none;
    color: #000;
  }

  .header-logo {
    width: 150px;
  }

  .inner-header {
    justify-content: space-between;
    .search-mobile {
      display: none;
      @media (max-width: 600px) {
        display: block;
      }
    }
  }

  .icons {
    width: 150px;
    justify-content: flex-end;
    display: flex;
    gap: 20px;
  }

  .header-nav {
    gap: 20px;
    display: flex;
    flex: 1;
    text-align: center;
    display: flex;
    justify-content: center;

    .header-nav-list {
      list-style: none;
      display: flex;
      gap: 20px;
      margin: 0;
      padding: 0;
      justify-content: center;
      li {
        a {
          font-size: 18px;
          border-bottom: 1px solid transparent;
        }
      }
    }
  }
}

/*
* Mobile / Burgermenu
*/
@media (max-width: 600px) {
  .header {
    justify-content: space-between;
    .header-logo {
      order: 1;
      width: fit-content;
    }
    .header-nav {
      position: absolute;
      .header-nav-list {
        display: none;
      }
    }
    .icons {
      order: 2;
      width: fit-content;
      width: 70px;
      .search {
        display: none;
      }
    }
    .inner-header {
      width: 70px;
      display: flex;
    }
  }

  .header-nav {
    display: none;
  }

  .menu-icon {
    display: inline-block;
    cursor: pointer;
    gap: 5px;
    display: flex;
    flex-direction: column;
    margin: 0;

    .bar {
      width: 30px;
      height: 2px;
      background: #000;
      transition: 0.4s;
    }
  }

  .menu-opened {
    .header-nav {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 50px;
      width: 100%;
      left: 0;
      padding: 20px 0;
      z-index: -1;
      .header-nav-list {
        display: flex !important;
        flex-direction: column;
        text-align: center;
        padding-top: 20px;
      }
    }
    .bar-1 {
      display: none;
    }

    .bar-2 {
      transform: rotate(-45deg) translate(-3px, 2px);
    }

    .bar-3 {
      transform: rotate(45deg) translate(-3px, -3px);
    }
  }
}
